import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/history",
        name: "History",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/History.vue"),
    },
    {
        path: "/match",
        name: "Match",
        component: () =>
            import ("../views/Match.vue"),
    },
    {
        path: "/sign",
        name: "Sign",
        component: () =>
            import ("../views/Sign.vue"),
    },
];

const router = new VueRouter({
    routes,
});

router.beforeEach((to, from, next) => {
    if (to.name === "Home") {
        next();
    }
    if (
        (to.name === "History" || to.name === "Match" || to.name === "Sign") &&
        from.name === "Home"
    ) {
        next();
    }
    if (
        (to.name === "Home" || to.name === "Match" || to.name === "Sign") &&
        from.name === "History"
    ) {
        next();
    }
    if (
        (to.name === "History" || to.name === "Home" || to.name === "Sign") &&
        from.name === "Match"
    ) {
        next();
    }
    if (
        (to.name === "History" || to.name === "Home" || to.name === "Match") &&
        from.name === "Sign"
    ) {
        next();
    }
    if (
        to.name === "History" &&
        from.name !== "Home" &&
        from.name !== "Match" &&
        from.name !== "Sign"
    ) {
        next({ path: "/" });
    }
    if (
        to.name === "Match" &&
        from.name !== "Home" &&
        from.name !== "History" &&
        from.name !== "Sign"
    ) {
        next({ path: "/" });
    }
    if (
        to.name === "Sign" &&
        from.name !== "Home" &&
        from.name !== "Match" &&
        from.name !== "History"
    ) {
        next({ path: "/" });
    }
});
export default router;