<template>
  <div v-if="flag == 1" v-bind:class="data.gender == '女' ? 'contentbox' : 'contentbox1'">
    <el-button
      v-show="likeFlag != 1"
      v-if="this.openid == data.ID"
      v-on:click="drop"
      class="drop"
      type="danger"
      plain
    >
      我要下墙
    </el-button>
    <p class="actor-title">
      {{ data.gender == "男" ? "男神" : "女神" }}
    </p>
    <div class="pic-content">
      <el-carousel>
        <el-carousel-item v-for="item in data.image" :key="item.index">
          <el-image style="height: 300px" :src="item" :fit="'contain'"></el-image>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div v-on:click="like" class="like" v-if="this.openid != data.ID">
      <img
        v-bind:class="isLiked == true ? 'heart-anim' : 'heart'"
        src="../assets/爱心.svg"
      />
      <span v-show="likeFlag!=1" v-bind:class="isLiked == true ? 'likes' : 'likes'">{{ data.like }}</span>
    </div>
    <div class="main-info">
      <div class="nickName">{{ data.name }}</div>
      <div class="school">{{ data.school }}</div>
      <div class="age">{{ data.age }}岁</div>
      <div class="height">{{ data.height }}cm</div>
      <div class="constellation">{{ data.star }}</div>
      <div class="place">{{ data.location }}</div>
    </div>
    <div class="sub-info"></div>
    <div class="about-me">关于我:<br />{{ data.introduction }}</div>
    <div class="target">我喜欢的TA: <br />{{ data.goal }}</div>
    <div v-if="likeFlag != 1" v-show="data.ispay != 0 || this.paied == 1" class="connect">
      联系方式:{{ data.connect }}
    </div>

    <el-button
      type="danger"
      v-show="likeFlag != 1 && data.ispay == 0&&this.openid!=data.ID"
      v-on:click="handlePayNotice"
      class="get-info"
      plain
    >
      获取联系方式
    </el-button>
    <p v-on:click="feedback" class="feedback">反馈</p>
    <div
      v-show="showFeedback"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div
      v-show="showPayNotice"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div
      v-show="showDrop"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div
      v-show="showFeedback"
      class="feedback-log"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    >
      <el-input
        type="textarea"
        :rows="5"
        class="question"
        v-model="question"
        placeholder="反馈问题"
      />
      <el-input
        class="feed-connect"
        v-model="feedConnect"
        type="text"
        placeholder="联系方式(QQ或邮箱等)"
      />
      <div class="button-content">
        <el-button type="danger" v-on:click="cancel" plain>取消</el-button>
        <el-button type="primary" v-on:click="submit" plain>{{ btnWord }}</el-button>
      </div>
    </div>
    <div
      v-show="showDrop"
      class="feedback-log"
      @scroll.stop.prevent
      @touchmove.stop.prevent
      style="height: 160px"
    >
      <el-input
        style="margin-bottom: 30px; padding-top: 10px"
        class="feed-connect"
        v-model="mail"
        placeholder="上墙时填写的邮箱"
      />
      <div class="button-content">
        <el-button type="danger" v-on:click="submitDrop" plain>下墙</el-button>
        <el-button type="primary" v-on:click="cancel" plain>取消</el-button>
      </div>
    </div>

    <div
      v-show="showPayNotice"
      class="feedback-log"
      @scroll.stop.prevent
      @touchmove.stop.prevent
      style="height: 200px; color: black"
    >
      <h4 style="margin-top: 20px;margin-bottom:5px">提示！</h4>
      <p style="margin-bottom:0px">您即将前往支付，支付后即可获取联系方式。</p>
      <p style="margin-top:0px">您需要同意<a href="http://proscgxtd.mikecrm.com/PxoydYR">支付协议</a></p>
      <div>
        <el-button v-on:click="closePayNotice" plain>取消</el-button>
        <el-button type="primary" v-on:click="getInfo" plain>同意并支付</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ContentBox",
  props: ["data", "openid", "flag", "likeFlag"], //flag控制是否显示此组件,likeFlag控制点赞是否单次
  data: function () {
    return {
      btnWord: "提交",
      question: "",
      feedConnect: "",
      showFeedback: false,
      isLiked: false,
      mail: "",
      showDrop: false,
      showPayNotice: false,
      paied: 0,
    };
  },
  mounted() {},
  methods: {
    drop: function () {
      this.showDrop = true;
    },
    like: function () {
      //先判断likeFlag,0为普通点赞,1为互选点赞
      console.log(this.data);
      // this.$data.isLiked = true;
      if (this.likeFlag == 0 && this.isLiked != true) {
        this.axios.post("http://www.scgxtd.cn/public/user/message/like", {
          actorUid: this.data.ID,
        });
        let preNum = Number(this.data.like);
        preNum += 1;
        this.isLiked = true;
        this.data.like = preNum;
        console.log(this.data.like);
        console.log(typeof this.data.like);
      } else if (this.likeFlag == 1) {
        this.isLiked = true;
        this.axios
          .post("http://www.scgxtd.cn/public/user/active/like", {
            openid: this.openid,
            actorid: this.data.ID,
          })
          .then((res) => {
            if (res.data.code == 0) {
              this.$message("点赞成功");
            } else if (res.data.code == -1) {
              this.$message("点赞失败:" + res.data.msg);
            }
          });
      }
      // console.log(this.openid);
      // if (this.openid != "" || this.openid != null)
      //   this.axios.post("url", {
      //     openid: this.openid,
      //     actorUid: this.data.uid,
      //   });
    },
    feedback: function () {
      this.$data.showFeedback = true;
    },
    submit: function () {
      this.btnWord = "正在提交";
      this.axios
        .post("http://www.scgxtd.cn/public/user/suggestion", {
          question: this.question,
          connect: this.feedConnect,
        })
        .then((res) => {
          this.btnWord = "提交";
          this.$data.showFeedback = false;
          if (res.data.code == 0) {
            this.$message("反馈成功");
          } else if (res.data.code == -1) {
            this.$message("反馈失败，msg:" + res.data.msg);
          }
        });
      console.log(this.$data.question);
      console.log(this.$data.feedConnect);
      this.$data.showFeedback = false;
    },
    submitDrop: function () {
      this.axios
        .post("/http://www.scgxtd.cn/public/user/message/quit", {
          openid: this.openid,
          email: this.mail,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("下墙成功");
          } else if (res.data.code == -1) {
            this.$message("下墙失败" + res.data.msg);
          }
        });
    },
    cancel: function () {
      this.$data.showFeedback = false;
      this.showDrop = false;
    },
    handlePayNotice: function () {
      this.showPayNotice = true;
    },
    closePayNotice: function () {
      this.showPayNotice = false;
      this.$emit("refresh");
    },
    getInfo: function () {
      // let p = this.$qs.stringify({

      // });
      console.log(this.data);
      this.axios
        .post("http://www.scgxtd.cn/public/index/wxpay/getjsparam", {
          openid: this.openid,
          actorid: this.data.ID,
        })
        .then((res) => {
          if (typeof WeixinJSBridge == "undefined") {
            if (document.addEventListener) {
              document.addEventListener("WeixinJSBridgeReady", this.onBridgeReady, false);
            } else if (document.attachEvent) {
              document.attachEvent("WeixinJSBridgeReady", this.onBridgeReady);
              document.attachEvent("onWeixinJSBridgeReady", this.onBridgeReady);
            }
          } else {
            this.onBridgeReady(res);
          }
        })
        .catch((err) => {
          console.log(err);
        });
      this.$alert("正在前往支付", "提示", {
        confirmButtonText: "完成支付，前往获取联系方式",
        callback: (action) => {
          if (action == "confirm") {
            this.showPayNotice = false;
            this.$emit("refresh");
          }
        },
      });
    },
    onBridgeReady(params) {
      // let myParam = JSON.stringify(params);
      // eslint-disable-next-line no-undef
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: params.data.appId, //公众号名称，由商户传入
          timeStamp: params.data.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: params.data.nonceStr, //随机串
          package: params.data.package,
          signType: "MD5", //微信签名方式：
          paySign: params.data.paySign, //微信签名
        },
        function (res) {
          this.data.ispay = 1;
          this.paied = 1;
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            this.$message("支付成功！正在刷新");
          }
        }
      );
    },
  },
};
</script>
